@import './color.scss';
.st-banner {
    background-image: url(./../images/about/banner-about.jpg);
    background-size: 100%;
    background-repeat: no-repeat;
    height: 400px;
    display: flex;
    align-items: center;

    h3 {
        font-size: 48px;
        color: #fff;
        position: relative;

        &::after {
            position: absolute;
            content: "";
            left: 0;
            bottom: 0;
            width: 10%;
            height: 4px;
            background-color: $cl-secondary;
        }
    }
}